@import url("https://fonts.googleapis.com/css2?family=Loved+by+the+King&display=swap");
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Loved by the King", cursive;
}
html {
  height: 100%;
  overflow-x: hidden;
}

/* for the root */
:root {
  --primary-color: #fcf5ee;
  --secondary-color: #fbe8e7;
  --tertiary-color: #f7ddde;
  --quaternary-color: #ffc4d0;
  --rgbabg: rgba(247, 221, 222, 0.3);
  --text-color: #9c7d01;
  --font-size-100: 0.75rem;
  --font-size-200: 1rem;
  --font-size-300: 1.2rem;
  --font-size-400: 1.7rem;
  --font-size-500: 2rem;
  --font-size-500: 2rem;
  --font-size-600: 2.5rem;
  --font-size-700: 2.7rem;
}

body {
  /* background color and text color default */
  background-color: var(--primary-color);
  color: var(--text-color);
  animation: bgfade 1s cubic-bezier(0.47, 0, 0.59, 1.16) forwards;
}
/* animation for the background to fade in */
@keyframes bgfade {
  0% {
    opacity: 0;
  }
  50% {
    backdrop-filter: blur(5px);
  }
  80% {
    opacity: 0.7;
  }
  100% {
    opacity: 1;
    backdrop-filter: blur(0px);
  }
}
